.container {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
    background-image: linear-gradient(to right, rgb(228, 224, 255), rgb(157, 150, 255) );
    border: 1px solid #e19aff;
    border-radius: 5px;
    position: relative;
}
.container h2{
    margin-left: 12%;
    color: rgb(77, 19, 202);
}
.form-group {
    margin-bottom: 20px;
}

.form-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color: rgb(0, 12, 143);
}

.form-group input,
.form-group select {
    width: 90%;
    padding: 10px;
    border-radius: 3px;
    border: 1px solid #ccc;
    font-size: 16px;
    margin: 10px;
}

.form-group input[type="date"] {
    padding: 10px;
    color: transparent;
}

.form-group input[type="date"]::-webkit-calendar-picker-indicator {
    filter: invert(1);
}

.error {
    color: rgb(200, 50, 255);
    font-size: 14px;
    margin-top: 5px;
}

button[type="submit"] {
    padding: 10px 20px;
    background-color: #3e51ca;
    color: #fff;
    border: none;
    border-radius: 3px;
    cursor: pointer;
}

button[type="submit"]:hover {
    background-color: #2b5b9a;
}

table {
    margin-top: 20px;
    width: 100%;
}

table td {
    padding: 5px 10px;
}

table td:first-child {
    font-weight: bold;
}